@use "../util/" as u;

.header{
    display: flex;
    flex-wrap: wrap;
    margin-top: u.rem(36);
    

    @include u.breakpoint(medium){
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    }

    &__title{
        width: 100%;
        @include u.breakpoint(medium){
           width: auto;
        }

    }
    &__subtitle{
        font-size: u.rem(14);
        font-weight: 700;
        color:var(--text-color2);
        
    }
}